.hero-area {
  // masthead
  box-shadow: inset 0px 0px 150px rgba(36, 36, 36, 0.7);
  height: 400px;
  min-height: 400px;
  background-image: url($hero-bg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;

  // rest
  padding: 170px 0 200px;
  position: relative;
  @include tablet {
    padding: 170px 0 150px;
    //overflow: hidden;
  }
  h1{
    position: relative;
    z-index: 2;
  }
}


.layer {
  position: absolute;
  z-index: 1;
}

#l1 {
  bottom: 0;
  left: 0;
}

#l2 {
  top: 190px;
  left: -250px;
}

#l3 {
  top: 200px;
  left: 40%;
}

#l4 {
  top: 200px;
  right: 40%;
}

#l5 {
  top: 100px;
  right: -150px;
}

#l6 {
  bottom: -20px;
  left: 10%;
}

#l7 {
  bottom: 100px;
  left: 20%;
}

#l8 {
  bottom: 160px;
  right: 45%;
}

#l9 {
  bottom: 100px;
  right: -10px;
}

.layer-bg {
  position: absolute;
  bottom: 0;
  left: 0;
}


.hero-image {
  margin-right: 30px;
  margin-left: 30px;
  //margin-top: 20px;
}